<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">Pay 支付（微信公众号，微信H5,微信小程序支付，微信扫码付，支付宝H5，支付宝扫码付）</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                支付组件，将针对常用的前端支付方式进行梳理，微信公众号，微信H5,微信小程序支付，微信扫码付，支付宝H5，支付宝扫码付。
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">1.微信公众号支付</div>
            <div class="hb-a">
                <p>微信公众号支付场景是在微信内打开的H5地址，既然在微信内打开就要按照微信的要求操作。</p>
                <p><strong>开发前要求：</strong></p>
                <p>a.需要有效期内的微信服务号。</p>
                <p>b.域名已经备案，并已经加入在微信公众号白名单里，业务域名，接口安全域名等已经加入。</p>
                <p>开发步骤：</p>
                <p>a.获取appId（根据具体业务，此步骤不一定需要）</p>
                <p>b.获取code （微信提供）</p>
                <p>前端把回调地址写好</p>
                <div v-highlight>
                        <pre>
                            <code >
                //根据业务 写好回调地址及参数
                let redirectURI = encodeURIComponent(`${location.origin}/wxPayRecharge?payAmount=${this.chargeMoney}`);
                //通过location 页面跳转到微信支付页，
                //只需要把 appid 和 redirect_uri 绑进去
                window.location = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirectURI}&response_type=code&scope=snsapi_base&state=WXPaySTATE#wechat_redirect`;
                //跳转到设定好的地址里 wxPayRecharge，url里 会带有code 参数
                            </code>
                        </pre>
                </div>
                <p>c.到wxPayRecharge回调支付页面里</p>
                <p>根据code 获取 openid （后端接口提供）</p>
                <p>d.根据openid 请求后端接口 获取appid,timestamp,nonceStr,sign(给jsapi那个接口使用)，才能调用微信公众号支付的方法（wx.chooseWXPay）</p>
                <p>e.唤起微信支付</p>
                <div v-highlight>
                        <pre>
                            <code >
                //timeStamp，nonceStr，package，signType，paySign 后端接口提供，d步接口返回
                wx.chooseWXPay({
                    timestamp: data.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                    nonceStr: data.nonceStr, // 支付签名随机串，不长于 32 位
                    package: data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                    signType: data.signType, // 微信支付V3的传入RSA,微信支付V2的传入格式与V2统一下单的签名格式保持一致
                    paySign: data.paySign, // 支付签名
                    success: function (res) {
                        // 支付成功后的回调函数
                        if (res.err_msg == "get_brand_wcpay_request:ok") {
                            location.href = `${location.origin}/storedValueCard`,   //根据业务要求 支付后返回地址

                        } else {
                            location.href = `${location.origin}/storedValueCard?status=err`,    //根据业务要求 支付后返回地址
                        }
                    }
                });
                            </code>
                        </pre>
                </div>
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">2.微信H5支付</div>
            <div class="hb-a">
                <p>微信H5支付场景是在非微信内打开的H5地址，比如用手机浏览器访问H5地址，通过浏览器拉起微信支付的功能。</p>
                <p><strong>开发步骤：</strong></p>
                <p>a.根据后端接口返回的url地址，前端重定向</p>
                <p>b.支付结果后跳转的页面，需要后端配置</p>
                <div v-highlight>
                        <pre>
                            <code >
                location.href = `${url}`;
                            </code>
                        </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">3.微信小程序支付</div>
            <div class="hb-a">
                <p>微信小程序支付场景是在微信小程序环境里。只能用小程序特有的支付方法，才能完成支付。</p>
                <div v-highlight>
                        <pre>
                            <code >
                wx.requestPayment({
                  timeStamp: '',
                  nonceStr: '',
                  package: '',
                  signType: 'MD5',
                  paySign: '',
                  success (res) { },
                  fail (res) { }
                })
                            </code>
                        </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">4.微信扫码付</div>
            <div class="hb-a">
                <p>微信扫码付场景是在PC端商城等。</p>
                <p>根据后端接口返回的数据，前端生成二维码，并把参数放在二维码里。</p>
                <p><img src="http://zsbg001.oss-cn-hangzhou.aliyuncs.com/base/1637897572310_93.23523587470972_eed3e88a-bfd1-4755-955f-6e245de69d25.png"  /></p>
                <p>在调用支付结果接口跳转，然后处理业务逻辑。</p>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">5.支付宝H5支付</div>
            <div class="hb-a">
                <p>支付宝H5支付场景是在手机浏览器内打开H5地址，通过浏览器拉起支付宝支付的功能。</p>
                <p><strong>开发步骤：</strong></p>
                <p>a.根据后端接口返回的url地址，前端重定向</p>
                <p>b.支付结果后跳转的页面，需要后端配置</p>
                <div v-highlight>
                        <pre>
                            <code >
                const form = url; //url为接口返回的地址
                const div = document.createElement("div");
                div.id = "alipay-recharge";
                div.innerHTML = form;
                document.body.appendChild(div);
                document.querySelector("#alipay-recharge").children[0].submit();
                            </code>
                        </pre>
                </div>
                <p>c.再调用支付结果接口跳转，然后处理业务逻辑。</p>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">6.支付宝扫码付</div>
            <div class="hb-a">
                <p>a.支付宝扫码付场景是在PC端商城等。</p>
                <p>b.根据后端接口返回的数据，前端生成二维码，并把参数放在二维码里。</p>
                <p>
                    <img src="http://zsbg001.oss-cn-hangzhou.aliyuncs.com/base/1637898044849_10.848060848183316_276e524d-03be-43c5-8e2c-5c1373e56c07.png"/>
                </p>
                <div v-highlight>
                        <pre>
                            <code >
                const div=document.createElement('div');
                div.innerHTML=d.data.url;
                $('body').prepend(div); //放在body里的最前面
                            </code>
                        </pre>
                </div>
                <p>c.再调用支付结果接口跳转，然后处理业务逻辑。</p>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">7.其他</div>
            <div class="hb-a">
                <p><a href="../../../static/files/paycode.zip" class="download-files">下载文件</a></p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'wltxtlinkhref',
        data() {
            return {

            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{

        }
    }
</script>
<style scoped>
    .h-headtitle{height: auto;}
    .download-files{color:#333;}
    .download-files:hover{text-decoration: underline;}
</style>



